.l-header {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  margin-top: clamp(80px, 12.5vw, 180px);
  margin-bottom: clamp(60px, 12.5vw, 180px);
  padding-right: 20px;
  padding-left: 20px;
  margin-bottom: 0;
  position: fixed;
  top: 15px;
  right: 0;
  left: 0;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
}
.l-header__logo {
  max-width: clamp(40px, 4.1666666667vw, 60px);
  width: 100%;
}
.l-header__menu {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 32px;
}
.l-header__list {
  display: flex;
  align-items: center;
  gap: clamp(16px, 2.2222222222vw, 32px);
  transition: 0.3s;
}
.l-header__list-item {
  width: fit-content;
}
.l-header__link {
  color: #fff;
  font-size: clamp(16px, 1.3888888889vw, 20px);
  font-weight: 500;
  letter-spacing: calc(32 / 1000 * 1em);
  line-height: normal;
  transition: 0.7s;
}
@media (hover: hover) {
  .l-header__link:hover {
    opacity: 0.7;
    transition: 0.3s;
  }
}

.l-header__hamburger {
  position: relative;
  z-index: 100;
}
.l-header__hamburger span {
  position: relative;
  display: block;
  width: 30px;
  height: 2px;
  border-radius: 10px;
  background: #fff;
  transition: 0.3s;
}
.l-header__hamburger::before, .l-header__hamburger::after {
  content: "";
  position: absolute;
  right: 0;
  left: 0;
  margin: auto;
  width: 30px;
  height: 2px;
  border-radius: 10px;
  background: #fff;
  transition: 0.3s;
}
.l-header__hamburger::before {
  top: -10px;
}
.l-header__hamburger::after {
  bottom: -10px;
}

.l-header__sp-nav {
  position: fixed;
  top: 0;
  right: -100%;
  bottom: 0;
  margin: auto;
  max-width: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 32px;
  width: 100%;
  height: 100lvh;
  transition: 0.3s;
  background: #f5eece;
}

.l-header__sp-link {
  font-size: clamp(24px, 1.6666666667vw, 24px);
  font-weight: 700;
  letter-spacing: calc(0 / 1000 * 1em);
  line-height: normal;
}

.is-open-hamburger {
  transition: 0.3s;
}
.is-open-hamburger span {
  opacity: 0;
  transition: 0.3s;
}
.is-open-hamburger::before, .is-open-hamburger::after {
  transition: 0.3s;
}
.is-open-hamburger::before {
  transform: rotate(25deg);
  top: 0;
}
.is-open-hamburger::after {
  transform: rotate(-25deg);
  bottom: 0;
}

.is-open-nav {
  right: 0;
  transition: 0.3s;
}

.is-past-kv::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(3px);
  z-index: -1;
}
@media (max-width: 1023px) {
  .is-past-kv::after {
    display: none;
  }
}
.is-past-kv a {
  color: #646361;
}
.is-past-kv .l-header__hamburger span,
.is-past-kv .l-header__hamburger::before,
.is-past-kv .l-header__hamburger::after {
  background: #646361;
}

.l-footer {
  padding-top: clamp(20px, 1.3888888889vw, 20px);
  padding-bottom: clamp(20px, 1.3888888889vw, 20px);
}

.l-footer__copy {
  font-size: clamp(16px, 1.1111111111vw, 16px);
  font-weight: 500;
  letter-spacing: calc(0 / 1000 * 1em);
  line-height: normal;
  text-align: center;
}

@keyframes floaty {
  0% {
    transform: translate(0, 0) rotate(0deg);
  }
  12% {
    transform: translate(-300px, 200px) rotate(0.4deg);
  }
  25% {
    transform: translate(400px, -100px) rotate(-0.6deg);
  }
  38% {
    transform: translate(-200px, -300px) rotate(1deg);
  }
  50% {
    transform: translate(100px, 0px) rotate(-0.2deg);
  }
  63% {
    transform: translate(-400px, 300px) rotate(0.8deg);
  }
  75% {
    transform: translate(300px, -200px) rotate(-0.9deg);
  }
  88% {
    transform: translate(-100px, 100px) rotate(0.3deg);
  }
  100% {
    transform: translate(0, 0) rotate(0deg);
  }
}
/* ローディングアニメーション */
@keyframes sk-scaleout {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
/* ローディング画面 */
.c-loading {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #ffdee4;
  overflow: hidden;
  transition: all 1s;
  z-index: 100;
}

.spinner {
  width: 100px;
  height: 100px;
  margin: 200px auto;
  background-color: #fff;
  border-radius: 100%;
  animation: sk-scaleout 1s infinite ease-in-out;
}

.loaded {
  opacity: 0;
  visibility: hidden;
}

.c-title {
  color: #ba9ea6;
  text-align: center;
  margin-bottom: 40px;
  font-size: clamp(24px, 2.9166666667vw, 42px);
  font-weight: 400;
  letter-spacing: calc(0 / 1000 * 1em);
  line-height: normal;
}

.p-kv {
  position: relative;
  width: 100%;
  height: 80vh;
  background: #efd8d5;
  overflow: hidden;
}
.p-kv__title {
  position: absolute;
  bottom: 30px;
  left: 10%;
  margin: auto;
  width: fit-content;
  height: fit-content;
  color: #fff;
  font-size: clamp(40px, 5.5555555556vw, 80px);
  font-weight: 400;
  letter-spacing: calc(42 / 1000 * 1em);
  line-height: normal;
  text-shadow: 3px 2px 20px #efd8d5;
  z-index: 1;
}
.p-kv__decoration {
  position: absolute;
  right: 0;
  background: linear-gradient(to right, #e3fdf5, #ffe6fa);
  filter: blur(100px);
  border-radius: 100%;
}
.p-kv__decoration:nth-of-type(1) {
  width: 600px;
  height: 600px;
  top: 0;
  animation: floaty 60s infinite;
}
.p-kv__decoration:nth-of-type(2) {
  width: 431px;
  height: 431px;
  right: 10%;
  bottom: 0;
  animation: floaty 120s -80s infinite;
}

.p-about {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  margin-top: clamp(80px, 12.5vw, 180px);
  margin-bottom: clamp(60px, 12.5vw, 180px);
  padding-right: 20px;
  padding-left: 20px;
}
.p-about__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1023px) {
  .p-about__wrap {
    flex-direction: column;
  }
}
.p-about__img {
  position: relative;
  width: 100%;
  max-width: 320px;
}
.p-about__img::after {
  content: "";
  position: absolute;
  top: 60px;
  right: 0;
  bottom: 0;
  left: 30px;
  margin: auto;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: #f5eece;
}
.p-about__content {
  width: 100%;
  max-width: 357px;
}
@media (max-width: 1023px) {
  .p-about__content {
    margin-top: 40px;
  }
}
.p-about__name {
  width: 100%;
  font-size: clamp(20px, 1.8055555556vw, 26px);
  font-weight: 400;
  letter-spacing: calc(0 / 1000 * 1em);
  line-height: 1.6;
}
.p-about__text {
  width: 100%;
  margin-top: 16px;
  font-size: clamp(16px, 1.1111111111vw, 16px);
  font-weight: 400;
  letter-spacing: calc(0 / 1000 * 1em);
  line-height: 1.6;
}

.p-skill {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  margin-top: clamp(80px, 12.5vw, 180px);
  margin-bottom: clamp(60px, 12.5vw, 180px);
  padding-right: 20px;
  padding-left: 20px;
}
.p-skill__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(clamp(100px, 8.3333333333vw, 120px), 1fr));
  gap: clamp(30px, 4.8611111111vw, 70px);
  justify-content: center;
  align-items: center;
}
.p-skill__list-item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 200px;
  width: 100%;
  text-align: center;
}
.p-skill__img {
  width: 100%;
  max-width: 120px;
  margin-bottom: 16px;
}
.p-skill__text {
  font-size: clamp(16px, 1.1111111111vw, 16px);
  font-weight: 400;
  letter-spacing: calc(0 / 1000 * 1em);
  line-height: normal;
}

.p-works {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  margin-top: clamp(80px, 12.5vw, 180px);
  margin-bottom: clamp(60px, 12.5vw, 180px);
  padding-right: 20px;
  padding-left: 20px;
  font-family: "Noto Serif JP", serif;
}
.p-works__list {
  display: grid;
  gap: 5;
  margin-bottom: 20px;
}
.p-works__list-item {
  font-size: clamp(16px, 1.1111111111vw, 16px);
  font-weight: 400;
  letter-spacing: calc(1.75 / 1000 * 1em);
  line-height: normal;
}
.p-works__sub {
  display: grid;
  gap: 5px;
}
.p-works__sub-item {
  font-size: clamp(12px, 0.8333333333vw, 12px);
  font-weight: 400;
  letter-spacing: calc(1.75 / 1000 * 1em);
  line-height: normal;
}
.p-works__content {
  margin-top: 60px;
}
.p-works__img {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
.p-works__text {
  margin-top: 80px;
  text-align: center;
  font-size: clamp(18px, 1.3888888889vw, 20px);
  font-weight: 400;
  letter-spacing: calc(32 / 1000 * 1em);
  line-height: normal;
}

.l-footer__copy {
  display: block;
  text-align: center;
}

.c-fade {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity var(--dur, 0.6s) var(--easing, ease), transform var(--dur, 0.6s) var(--easing, ease);
  transition-delay: var(--delay, 0s);
  will-change: opacity, transform;
}

/* 画面内に入ったら付与される */
.is-inview {
  opacity: 1;
  transform: none;
}